.banner {
  height: 158px;
  background: #89706e url("../../img/back-desktop.jpg") no-repeat 50% 100%;
  background-size: cover;
  padding-top: 52px;
  text-align: center;
  position: relative;

  &::after {
    @include bottom-triangle("../../img/intro-triangle.svg", 1200px 56px, 56px);
  }

  @media (min-width: $tablet-width) {
    height: 290px;
  }
}
